button {
  cursor: pointer;
  font-size: 20px;
  font-weight: bold;
  margin-right: 35px;
  padding: 6px;
  width: auto;
  border-radius: 10px;
  background-color: black;
  color: whitesmoke;
  border: 2px solid whitesmoke;
  box-shadow: rgba(240, 46, 170, 0.4) 0px 5px, rgba(240, 46, 170, 0.3) 0px 10px, rgba(240, 46, 170, 0.2) 0px 15px, rgba(240, 46, 170, 0.1) 0px 20px, rgba(240, 46, 170, 0.05) 0px 25px;
}

button:active {
  color: rgb(240, 46, 170);
  border: 2px solid rgb(240, 46, 170);
  box-shadow: rgba(245, 245, 245, 0.4) 0px 5px, rgba(245, 245, 245, 0.3) 0px 10px, rgba(245, 245, 245, 0.2) 0px 15px, rgba(245, 245, 245, 0.1) 0px 20px, rgba(245, 245, 245, 0.05) 0px 25px;
}

.navbar {
  display: flex;
  align-items: center;
  padding: 15px;
  background-color: rgba(0, 0, 0, 0.75);
  height: 10%;
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 100;
}

.tainted-peach-image {
  height: 100%;
  border-radius: 50px;
  margin-right: 10px;
  box-shadow: 0 0 25px rgb(240, 46, 170);
}

.nav-title {
  margin-right: auto;
  color: whitesmoke;
  text-shadow: rgba(240, 46, 170, 0.4) 0px 5px, rgba(240, 46, 170, 0.3) 0px 10px, rgba(240, 46, 170, 0.2) 0px 15px, rgba(240, 46, 170, 0.1) 0px 20px, rgba(240, 46, 170, 0.05) 0px 25px;
  font-size: 50px;
}

.hidden {
  display: none;
}